@font-face {
	font-family: 'Gilroy';
	font-style: normal;
	font-weight: bold;
	src: url("../font/Gilroy-Bold.woff") format('woff');
}


@font-face {
	font-family: 'Gilroy';
	font-style: normal;
	font-weight: bolder;
	src: url("../font/Gilroy-Heavy.woff") format('woff');
}


@font-face {
	font-family: 'Gilroy-Light';
	font-style: normal;
	font-weight: lighter;
	src: url("../font/Gilroy-Light.woff") format('woff');
}


@font-face {
	font-family: 'Gilroy';
	font-style: normal;
	font-weight: 500;
	src: url("../font/Gilroy-Medium.woff") format('woff');
}


@font-face {
	font-family: 'Gilroy';
	font-style: normal;
	font-weight: normal;
	src: url('../font/Gilroy-Regular.woff') format('woff');
}

@font-face {
	font-family: 'Monument Extended';
	font-style: normal;
	font-weight: normal;
	src: url("../font/Monument\ Extended.woff") format('woff');
}


* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

img {
	max-width: 100%;
}

header {
	background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%), #EBF3FF;
	padding-bottom: 90px;
}


.header {
	background-color: white;
	box-shadow: 0px 4px 30px 5px rgba(0, 0, 0, 0.04);
	position: relative;
	z-index: 2;
}

header .header-div {
	max-width: 1200px;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 72px;
}

header .header-div .header-menu ul {
	display: flex;
	align-items: center;
	list-style: none;
}

header .header-div .header-menu ul li {
	margin-right: 82px;
}

header .header-div .header-menu ul li:last-child {
	margin-right: 0;
}

header .header-div .header-menu ul li span {
	font-family: 'Gilroy', sans-serif;
	font-weight: bold;
	color: #363636;
	text-decoration: none;
	text-transform: uppercase;
	letter-spacing: 0.065em;
	cursor: pointer;
}

.mobile-menu {
	display: none;
}

/* jumbotron */
.jumbotron {
	max-width: 1200px;
	margin: 0 auto;
	display: grid;
	grid-template-columns: 1fr 1fr;
	align-items: flex-end;
}



.jumbotron .text-1 {
	font-family: 'Gilroy', sans-serif;
	font-weight: bold;
	color: #363636;
	letter-spacing: 0.065em;
	font-size: 16px;
	display: flex;
	align-items: center;
	text-transform: uppercase;
	margin-bottom: 16px;
}

.jumbotron .text-1>span {
	margin-left: 12px;
}

.jumbotron .text-2 {
	font-family: 'Monument Extended', sans-serif;
	font-style: normal;
	font-weight: bold;
	font-size: 36px;
	line-height: 49px;
	letter-spacing: -0.02em;
	color: #000000;
	margin-bottom: 16px;
}

.jumbotron .text-2 span {
	color: #458bec
}

.jumbotron .text-3 {
	font-family: 'Gilroy', sans-serif;
	font-size: 18px;
	line-height: 151.7%;
	letter-spacing: -0.02em;
	color: #363636;
	margin-bottom: 60px;
}

a.join-slack {
	text-decoration: none;
}

.join-slack-button {
	display: inline-flex;
	width: 320px;
	height: 60px;
	box-shadow: 0px 4px 20px 10px rgba(0, 0, 0, 0.05);
}

.join-slack-button .join-slack-button-div1 {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-grow: 1;
	font-family: 'Gilroy', sans-serif;
	font-size: 18px;
	line-height: 150.7%;
	text-align: center;
	letter-spacing: -0.01em;
	background: #4588EC;
	color: #FFFFFF;
}

.join-slack-button .join-slack-button-div2 {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 60px;
	background: #165EC9;
}

.jumbotron .jumbotron-div1 .circle-down {
	margin-top: 52px;
}

.jumbotron .jumbotron-div1 .circle-down  svg {
	cursor: pointer;
}

.jumbotron .jumbotron-div2 {
	text-align: right;
	padding-top: 48px;
}

.header-carousel-icon {
	text-align: center;
	transform: translateY(-50%);
}

.header-carousel-icon svg {
	cursor: pointer;
}


.available-courses {
	background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 1000%), rgba(253, 217, 97, 0.14);
	height: 512px;
	position: relative;
	/* background-color: green; */
}

.available-courses>div {
	position: absolute;
	top: 124px;
	width: 100%;
}

.available-courses h2.pricing {
	text-transform: uppercase;
	font-family: 'Gilroy', sans-serif;
	font-weight: bold;
	font-size: 16px;
	line-height: 155.47%;
	text-align: center;
	letter-spacing: 0.065em;
	color: #363636;
	position: relative;
}

.available-courses h2.pricing::before {
	content: "";
	height: 2px;
	width: 80px;
	background-color: #f1f1f1;
	position: absolute;
	top: 50%;
	transform: translateX(-112px) translateY(-50%);
}

.available-courses h2.pricing::after {
	content: "";
	height: 2px;
	width: 80px;
	background-color: #f1f1f1;
	position: absolute;
	top: 50%;
	transform: translateX(32px) translateY(-50%);
}


.available-courses h4 {
	font-family: 'Monument Extended', sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 36px;
	line-height: 101.1%;
	text-align: center;
	letter-spacing: -0.02em;
	color: #4588EC;
	margin-top: 4px;
}


.available-courses .pricing-div {
	max-width: 1260px;
	background: #FFFFFF;
	border: 1px solid #F1F1F1;
	border-radius: 20px;
	margin: 0 auto;
	margin-top: 80px;
	position: relative;
	padding: 120px 52px 100px 52px;
	min-height: 470px;
}


.available-courses .pricing-div .sql-or-excel {
	display: grid;
	grid-template-columns: 1fr 1fr;
	position: absolute;
	top: 0;
	left: 50%;
	transform: translateX(-50%) translateY(-50%);
	background: #F7FAFF;
	border: 1px solid #F1F1F1;
	box-sizing: border-box;
	border-radius: 16px;
	padding: 6px;
}

.available-courses .pricing-div .sql-or-excel>svg {
	bottom: 0;
	left: 50%;
	transform: translateX(-50%) translateY(100%);
	position: absolute;
}

.available-courses .pricing-div .sql-or-excel>div {
	border-radius: 10px;
	height: 72px;
	width: 252px;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
}

.available-courses .pricing-div .sql-or-excel>div span {
	font-family: 'Monument Extended', sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 36px;
	letter-spacing: -0.02em;
	color: #AFAFAF;
	margin-left: 8px;
}

.available-courses .pricing-div .sql-or-excel>div.active {
	background-color: white;
	border: 1px solid #EFEFEF;
	box-shadow: 0px 4px 15px 6px rgba(0, 0, 0, 0.05);
	cursor: auto;
}

.available-courses .pricing-div .sql-or-excel>div.active span {
	color: #000000;
}

.available-courses .pricing-div .prices {
	align-items: start;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	gap: 28px;
}

.available-courses .pricing-div .prices>div {
	padding: 48px 36px;
	background: #FFFFFF;
	border: 1px solid #E2E2E2;
	border-radius: 10px;
	position: relative;
}

.available-courses .pricing-div .prices .plan-name {
	font-family: 'Gilroy', sans-serif;
	font-weight: bold;
	text-transform: uppercase;
	font-size: 18px;
	line-height: 155.47%;
	letter-spacing: 0.065em;
	color: #AEAEAE;
}

.available-courses .pricing-div .prices .plan-price {
	font-family: 'Monument Extended', sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 36px;
	line-height: 101.1%;
	letter-spacing: -0.02em;
	color: #4588EC;
}

.available-courses .pricing-div .prices .plan-description {
	margin-top: 18px;
	/* font-family: 'Gilroy-Regular', sans-serif; */
	font-family: 'Gilroy', sans-serif;
	font-size: 16px;
	line-height: 151.7%;
	letter-spacing: -0.02em;
	color: #646464;
}

.available-courses .pricing-div .prices .plan-time {
	margin-top: 8px;
	font-family: 'Gilroy', sans-serif;
	font-weight: bold;
	font-size: 16px;
	line-height: 151.7%;
	letter-spacing: -0.02em;
	color: #646464;
}

.available-courses .pricing-div .prices .plan-banner {
	font-family: 'Monument Extended', sans-serif;
	font-size: 48px;
	position: absolute;
	bottom: 36px;
	color: rgba(69, 136, 236, 0.1);
}
.available-courses .pricing-div .prices .start-here {
	display: none;
	opacity: 0;
	text-decoration: none;
	cursor: pointer;
}

.available-courses .pricing-div .prices .start-here-div {
	display: inline-flex;
	height: 50px;
	width: 200px;
	box-shadow: 0px 4px 20px 10px rgba(0, 0, 0, 0.05);
	margin-top: 16px;
}

.available-courses .pricing-div .prices .start-here-div-div1 {
	text-transform: uppercase;
	background: #FDD961;
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: 'Gilroy', sans-serif;
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 150.7%;
	letter-spacing: -0.01em;
	color: #000000;
	flex-grow: 1;
}

.available-courses .pricing-div .prices .start-here-div-div2 {
	flex-basis: 50px;
	background: #ECBA13;
	display: flex;
	justify-content: center;
	align-items: center;
}

.available-courses .pricing-div .prices>div:hover {
	background: #4588EC;
	transition: all 1s ease;
}

.available-courses .pricing-div .prices>div:hover .plan-name {
	color: #BDD8FF;
	transition: all 1s ease;
}

.available-courses .pricing-div .prices>div:hover .plan-price {
	color: #FFFFFF;
	transition: all 1s ease;
}

.available-courses .pricing-div .prices>div:hover .plan-description {
	color: #FFFFFF;
	transition: all 1s ease;
}

.available-courses .pricing-div .prices>div:hover .plan-time {
	color: #FFFFFF;
	transition: all 1s ease;
}

.available-courses .pricing-div .prices>div:hover .plan-banner {
	color: rgba(255, 255, 255, 0.1);
	bottom: calc(50px + 16px + 36px);
}

.available-courses .pricing-div .prices>div:hover  .start-here {
	display: inline;
	opacity: 1;
	transition: opacity 1s ease;
}

.available-courses .coming-soon {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.available-courses .coming-soon h4 {
	font-style: normal;
	font-weight: normal;
	font-size: 36px;
	line-height: 101.1%;
	margin-top: 28px;
	text-align: center;
	letter-spacing: -0.02em;

	color: #000000;
}

.available-courses .coming-soon p {
	margin-top: 16px;
	margin-bottom: 16px;
	font-family: 'Gilroy', sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 151.7%;
	text-align: center;
	letter-spacing: -0.02em;
	color: #AFAFAF;
}

/* join our slack */
.join-our-slack {
	padding-bottom: 120px;
	background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%), rgba(235, 243, 255, 0.5);
	padding-top: 352px;
}

.join-our-slack .join-our-slack-grid {
	max-width: 1200px;
	margin: 0 auto;
	display: grid;
	grid-template-columns: 1fr 1fr;
}

.join-our-slack .join-our-slack-grid .join-our-slack-grid-div1 {
	padding-right: 108px;
}

.join-our-slack .join-our-slack-grid .join-our-slack-text {
	margin-top: 48px;
}

.join-our-slack .join-our-slack-grid .join-our-slack-text span {
	text-transform: uppercase;
	display: block;
	text-align: right;
	position: relative;
	font-family: 'Gilroy', sans-serif;
	font-weight: bold;
	font-size: 16px;
	line-height: 155.47%;
	letter-spacing: 0.065em;
	color: #363636;
}

.join-our-slack .join-our-slack-grid .join-our-slack-text span::before {
	content: "";
	height: 2px;
	width: 80%;
	background-color: #F1F1F1;
	position: absolute;
	top: 50%;
	left: 0;
	transform: translateY(-50%);
}

.join-our-slack .join-our-slack-grid .join-our-slack-text h4 {
	margin-top: 4px;
	font-family: 'Monument Extended', sans-serif;
	text-transform: capitalize;
	font-style: normal;
	font-weight: normal;
	font-size: 36px;
	line-height: 101.1%;
	letter-spacing: -0.02em;
	color: #4588EC;
}

.join-our-slack .join-our-slack-grid .join-our-slack-text p {
	margin-top: 28px;
	margin-bottom: 32px;
	font-family: 'Gilroy', sans-serif;
	font-size: 18px;
	line-height: 151.7%;
	letter-spacing: -0.02em;
	color: #363636;
}

/* .about-us-and-testimonials */
.about-us-and-testimonials {
	background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%), rgba(253, 217, 97, 0.14);
	padding-bottom: 172px;
}

/* .about-us */
.about-us {
	max-width: 1200px;
	margin: 0 auto;
	padding-top: 140px;
}

.about-us-grid {
	display: flex;
	align-items: center;
	flex-wrap: nowrap;
}

.about-us-grid .about-us-grid-div1 {
	margin-right: 188px;
}

.about-us-grid .about-us-grid-text span {

	text-transform: uppercase;
	display: inline-block;
	width: 200px;
	text-align: right;
	position: relative;
	font-family: 'Gilroy', sans-serif;
	font-weight: bold;
	font-size: 16px;
	line-height: 155.47%;
	letter-spacing: 0.065em;
	color: #363636;
}

.about-us-grid .about-us-grid-text span::before {
	content: "";
	height: 2px;
	width: 35%;
	background-color: #F1F1F1;
	position: absolute;
	top: 50%;
	left: 0;
	transform: translateY(-50%);
	text-transform: capitalize;
}

.about-us-grid .about-us-grid-text h4 {
	margin-top: 4px;
	font-family: 'Monument Extended', sans-serif;
	text-transform: capitalize;
	font-style: normal;
	font-weight: normal;
	font-size: 36px;
	line-height: 101.1%;
	letter-spacing: -0.02em;
	color: #4588EC;
}

.about-us-grid .about-us-grid-text p {
	margin-top: 28px;
	margin-bottom: 32px;
	font-family: 'Gilroy', sans-serif;
	font-size: 18px;
	line-height: 151.7%;
	letter-spacing: -0.02em;
	color: #363636;
	max-width: 500px;
}

.about-us-grid .people-trained {
	display: grid;
	grid-template-columns: 1fr ;
	gap: 60px;
	margin-bottom: 48px;
}

.about-us-grid .people-trained .main-text {
	font-family: 'Monument Extended', sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 64px;
	line-height: 101.1%;
	letter-spacing: -0.02em;
	color: #3EF1A8;
	display: block;
}

.about-us-grid .people-trained .sub-text {
	font-family: 'Gilroy', sans-serif;
	font-size: 18px;
	line-height: 151.7%;
	letter-spacing: -0.02em;
	color: #363636;
}

.testimonials {
	padding-top: 64px;
	max-width: 1200px;
	margin: 0 auto;
}

.testimonials .feedback {
	text-transform: uppercase;
	font-family: 'Gilroy', sans-serif;
	font-weight: bold;
	font-size: 16px;
	line-height: 155.47%;
	text-align: center;
	letter-spacing: 0.065em;
	color: #363636;
	position: relative;
}

.testimonials .feedback::before {
	content: "";
	height: 2px;
	width: 80px;
	background-color: #f1f1f1;
	position: absolute;
	top: 50%;
	transform: translateX(-112px) translateY(-50%);
}

.testimonials .feedback::after {
	content: "";
	height: 2px;
	width: 80px;
	background-color: #f1f1f1;
	position: absolute;
	top: 50%;
	transform: translateX(32px) translateY(-50%);
}

.testimonials h4 {
	font-family: 'Monument Extended', sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 36px;
	line-height: 101.1%;
	text-align: center;
	letter-spacing: -0.02em;
	color: #4588EC;
	margin-top: 4px;
}

.testimonials .testimonials-carousel {
	margin-top: 32px;
	position: relative;
}

.testimonials .testimonials-carousel .carousel-icon-left {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	left: 0;
}

.testimonials .testimonials-carousel .carousel-icon-right {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 0;
}

.testimonials .testimonials-carousel .carousel-icon-bottom {
	position: absolute;
	left: 50%;
	transform: translateX(-50%) translateY(100%);
	bottom: -24px;
}

.testimonials .testimonial-card-section {
	max-width: 1084px;
	margin: 0 auto;
	display: flex;
	list-style: none;
}

.testimonials .testimonial-card-section>li {
	margin-left: 30px;
	margin-right: 30px;
}

.testimonials .testimonial-card {
	border: 1px solid #E2E2E2;
	border-radius: 16px;
	padding: 64px 52px;
	min-height: 280px;
	max-width: 512px;
}

.testimonials .testimonial-card .testimonial-card-text {
	font-family: 'Gilroy', sans-serif;
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 150%;
	color: #646464;
}

.testimonials .testimonial-card .testimonail-persona {
	margin-top: 28px;
	display: flex;
	align-items: center;
}

.testimonials .testimonial-card .testimonail-persona img {
	border-radius: 50%;
	border: 5px solid rgba(255, 201, 40, 0.42);
	margin-right: 12px;
}

.testimonials .testimonial-card .testimonail-persona h6 {
	font-family: 'Monument Extended', sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 22px;
	color: #231F20;
}

.testimonials .testimonial-card .testimonail-persona span {
	font-family: 'Gilroy', sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 22px;
	color: #A19B9D;
}

.testimonials .testimonial-card:hover {
	background: #33C481;
	transition: all 1s ease;
}

.testimonials .testimonial-card:hover .testimonial-card-text {
	color : white;
	transition: all 1s ease;
}

.testimonials .testimonial-card:hover .testimonail-persona h6 {
	color : white;
	transition: all 1s ease;
}

.testimonials .testimonial-card:hover .testimonail-persona span{
	color : white;
	transition: all 1s ease;
}

/* footer */

footer {
	background: #4588EC;
}


footer .footer-div {
	margin-top: 40px;
	max-width: 1200px;
	margin-left: auto;
	margin-right: auto;
}

footer .footer-div .footer-section1 {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	flex-wrap: wrap;
}

footer .footer-div .footer-section1 .logo {}

footer .footer-div .footer-section1 .socials {
	display: flex;
	margin-top: 28px;
}

footer .footer-div .footer-section1 .socials a {
	margin-left: 8px;
}

footer .footer-div .footer-section1 .socials a .email-icon {
	height: 25px;
	width: 25px;
	border-radius: 1000px;
	background-color: white;
	display: flex;
	justify-content: center;
	align-items: center;
}

footer .footer-div .footer-section1 .socials a .email-icon .icon {
	color: #4588EC;
}

footer .footer-div .footer-section1 .socials img:first-child {
	margin-left: 0;
}

footer .footer-div .footer-section1 .footer-menu {
	display: flex;
}

footer .footer-div .footer-section1 .footer-menu ul {
	list-style: none;
	margin-left: 92px;
}

footer .footer-div .footer-section1 .footer-menu ul:first-child {
	margin-left: 0;
}

footer .footer-div .footer-section1 .footer-menu ul li:first-child {
	font-family: 'Monument Extended', sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 151.7%;
	letter-spacing: -0.02em;
	color: #FFFFFF;
	margin-bottom: 12px;
}

footer .footer-div .footer-section1 .footer-menu ul li {
	margin-bottom: 8px;
	text-align: center;
}

footer .footer-div .footer-section1 .footer-menu ul li:last-child {
	margin-bottom: 0px;
}

footer .footer-div .footer-section1 .footer-menu ul li span {
	font-family: 'Gilroy', sans-serif;
	font-style: normal;
	font-weight: 500;
